import React, { useState } from 'react';
import entries from './services/entries';

function FileUpload() {
  const [selectedFile, setSelectedFile] = useState(null);
  const [statusOfUpload, setStatusOfUpload] = useState('');
  const [newAuthor, setNewAuthor] = useState('');
  const [newTitle, setNewTitle] = useState('');

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
  };

  const handleMongo = () => {
    const text = {
      "title":"Mongoid",
      "author":"fellow kid",
      "pages": {
        "1":"Hansin tavallinen piv. aakkosellisuus Aakko Hern arkisin aina kello 7. Pidn rauhallisista aamuista, joten hern yleens ajoissa. Viikonloppuisin saatan nukkua hieman pidempn. Aamulla teen aamupalaa ja keitn kahvin. Aamupalaa syn lukiessa pivn lehte. Yleens syn kaurapuuroa lisukkeilla, mutta joskus saatan tehd voileivn tai syd jugurttia myslill.Sitten vaihdan vaatteet ja valmistaudun typivn. Typiv alkaa kello 9, joten lhden kotoa aina kello 8:30. Menen tihin linja-autolla. Aamuisin on yleens ruuhkaa ja bussi on melkein aina tynn. Joskus olen tiss vasta kello 9:10. Pivll kyn tykavereiden kanssa lounaalla ravintolassa. Olen tiss kello 17 asti. Onneksi en j koskaan ylitihin. Tiden jlkeen hoidan usein keskustassa asioita, kyn kaupassa tai tapaan ystvi. Sitten menen kotiin. Joskus kyn illalla kuntosalilla, katson televisiota tai luen kirjaa. Joskus teen vhn tit kotona illalla. Joko min tai tyttystvni tekee illallisen - yleens vuorottelemme. Illalla katsomme aina kymmenen uutiset ja sen jlkeen aloitamme iltapuuhat ja menemme nukkumaan."  
    },
    "pageTranslations": {
        "1":{
            "Hansin":"Hansi",
            "tavallinen":"tavallinen",
            "piv":"piv"
        }
    }
    }
    entries
      .create(text)
      .then(res =>
        console.log("Response from handle mongo: ",res))
      .catch(err => 
        console.log("Handle mongo failed: ",err))
  }

  const handleUpload = () => {
    if (selectedFile) {
      const formData = new FormData();
      formData.append('file', selectedFile);
      if (newAuthor == '' || newTitle == '') {
        return
      }
      //const endpoint = `http://127.0.0.1:5000/upload`
      const endpoint = `https://langapp23flask.onrender.com/upload`;
      fetch(endpoint, {
        method: 'POST',
        body: formData,
      })
      .then((response) => response.json())
      .then((data) => {
        console.log(data); // Access the response from Flask
        console.log("data.message, ", data.message); 
        console.log("data.dict, ", data.dict); 
        console.log("data.text, ", data.text); 
        setStatusOfUpload(data.message)
        const text = {
          "title":newTitle,
          "author":newAuthor,
          "pages": {
            "1":data.text  
        },
        "pageTranslations": {
            "1":data.dict
        }
      }
        entries
          .create(text)
          .then(res =>
            console.log("Response from handle mongo: ",res))
          .catch(err => 
            console.log("Handle mongo failed: ",err))
        
        // You can update the React state or perform any other actions based on the response.
      })
        .catch((error) => {
          console.error('Error occurred while uploading the file:', error);
        });
    }
  };

  const handleTitleChange = (event) => {
    setNewTitle(event.target.value)
  }


  const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value)
  }

  return (
    <div>
      <p>{statusOfUpload}</p>
      <input type="file" onChange={handleFileChange} />
      <input onChange= { handleAuthorChange} type='text' placeholder='Author'/>
      <input onChange= { handleTitleChange} type='text' placeholder='Title'/>
      <button onClick={handleUpload}>Upload</button>
      <button onClick={handleMongo}>Mongo!</button>
    </div>
  );
}

export default FileUpload;